"use strict"

export default function(query) {
    const _query = {
        "query": {
            "match_all": {}
        }
    }
    if (query) {
        _query.query = query
    }
    const options = {
        index: this.index,
        type: this.type,
        body: _query,
    }
    return this.client
		.count(options)
}


